<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="全部" name="-1"></el-tab-pane>
        <el-tab-pane label="进行中" name="1"></el-tab-pane>
        <el-tab-pane label="未开始" name="0"></el-tab-pane>
        <el-tab-pane label="已结束" name="2"></el-tab-pane>
        <el-tab-pane label="已失效" name="3"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="优惠券名称：">
            <el-input style="width: 203px" v-model="searchList.couponName" clearable placeholder="活动名称"></el-input>
          </el-form-item>
          <el-form-item label="类型：">
            <el-select v-model="searchList.couponType" placeholder="请选择类型">
              <el-option label="满减券" value="0"></el-option>
              <el-option label="开通会员专属券" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="newShow = true">新增优惠券</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="filter-container mt10" shadow="never">
      <el-table :data="list" border style="width: 100%">
        <el-table-column prop="couponName" label="优惠券名称" width="300"></el-table-column>
        <el-table-column label="优惠券面值" width="140">
          <template slot-scope="scope">
            <div>{{scope.row.discountAmount ? '￥'+scope.row.discountAmount : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="优惠内容" width="300"></el-table-column>
        <el-table-column label="类型" width="140">
          <template slot-scope="scope">
            <div v-if="scope.row.couponType == 0">满减券</div>
            <div v-if="scope.row.couponType == 1">会员专享券</div>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="100">
          <template slot-scope="scope">
            <div class="warningFont" v-if="scope.row.couponStatus == 0">未开始</div>
            <div class="succeseFont" v-if="scope.row.couponStatus == 1">进行中</div>
            <div class="dangerFont" v-if="scope.row.couponStatus == 2">已结束</div>
            <div class="dangerFont" v-if="scope.row.couponStatus == 3">已失效</div>
          </template>
        </el-table-column>
        <el-table-column label="已领取/发放总数" width="180">
          <template slot-scope="scope">
            <div>{{scope.row.couponHistoryNum + ' / '}}{{scope.row.couponCount ? scope.row.couponCount : '--'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="couponUserNum" label="已使用" width="140"></el-table-column>
        <el-table-column label="使用期限" width="300">
          <template slot-scope="scope">
            <div>{{scope.row.dateUsed ? scope.row.dateUsed : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="300">
          <template slot-scope="scope">
            <!-- 
              couponStatus 优惠券状态 0：未开始 1：进行中 2：已结束 3：已失效
              couponType 优惠券类型 0：满减券 1：会员专享券
             -->
            <!-- 数据 -->
            <el-button 
              type="text" 
              size="small" 
              @click="showData(scope.row)"
            >数据</el-button>
            <!-- 编辑 -->   
            <el-button 
              v-if="(scope.row.couponType == 0 && scope.row.couponStatus == 0) || (scope.row.couponType == 0 && scope.row.couponStatus == 1)"
              type="text" 
              size="small" 
              @click="$router.push({path:'/marketManage/coupon/editCoupon',query:{couponId:scope.row.id}})"
            >编辑</el-button>
            <!-- 查看 -->
            <el-button 
              type="text" 
              size="small" 
              @click="lookItem(scope.row)"
            >查看</el-button>
            <!-- 失效 -->
            <el-button 
              class="dangerFont"
              v-if="scope.row.couponStatus == 0 || scope.row.couponStatus == 1"
              type="text" 
              size="small"
              @click="failItem(scope.row)"
            >失效</el-button>
            <!-- 复制 -->
            <el-button 
              type="text" 
              size="small"
              @click="copyItem(scope.row)"
            >复制</el-button>
            <!-- 推广 -->
            <el-button 
              v-if="(scope.row.couponType == 0 && scope.row.couponStatus == 0) || (scope.row.couponType == 0 && scope.row.couponStatus == 1)"
              @click="extendItem(scope.row)"
              type="text" 
              size="small"
            >推广</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </div>
    </el-card>
    <!-- 新增优惠券选择框 -->
    <el-dialog title="新增优惠券类型" :visible.sync="newShow" width="30%" >
      <div>
        <el-radio v-model="couponType" label="0">满减券</el-radio>
      </div>
      <div class="mt10">
        <el-radio v-model="couponType" label="1">开通会员专属券</el-radio>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newShow = false">取 消</el-button>
        <el-button type="primary" @click="goToNew">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 数据弹出框 -->
    <el-dialog title="优惠券数据统计" :visible.sync="dataShow" width="70%" >
      <el-card class="filter-container" shadow="never">
        <div class="pageHead flex-row-between">
          <div>优惠券名称：{{dataShowSearch.couponName}}</div>
          <div class="infoFont">数据统计至前一天</div>
        </div>
        <el-row class="mt10" :gutter="12">
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>用券总成交额</span>
                <el-tooltip class="item" effect="dark" content="使用该优惠券的订单付款总金额，剔除退款金额" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.orderAmount}}</div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>优惠总金额</span>
                <el-tooltip class="item" effect="dark" content="使用该优惠券优惠的总金额，剔除退款金额" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.discountAmountTotal}}</div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>费效比</span>
                <el-tooltip class="item" effect="dark" content="优惠总金额 / 用券总成交额，剔除发生退款行为" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.discountRate}}</div>
            </el-card>
          </el-col>
        </el-row>
        <el-row class="mt10" :gutter="12">
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>用券比单价</span>
                <el-tooltip class="item" effect="dark" content="用券总成交额 / 使用该优惠券的付款订单数，剔除发生退款行为" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.couponRatio}}</div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>用券老客户</span>
                <el-tooltip class="item" effect="dark" content="使用该优惠券的老成交客户数，不剔除整单退款行为" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.oldUuserCount}}</div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>用券新客户</span>
                <el-tooltip class="item" effect="dark" content="使用该优惠券的新成交客户数（首次下单的客户），不剔除整单发生退款行为" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.newUserCount}}</div>
            </el-card>
          </el-col>
        </el-row>
        <el-row class="mt10" :gutter="12">
          <el-col :span="8">
            <el-card shadow="hover">
              <div class="flex-row-start">
                <span>购买商品件数</span>
                <el-tooltip class="item" effect="dark" content="使用该优惠券购买的商品数量，剔除发生退款行为" placement="top">
                  <i style="margin-left:4px;" class="el-icon-question curP"></i>
                </el-tooltip>
              </div>
              <div class="mt10 f20 fb">{{dataShowForm.productCount}}</div>
            </el-card>
          </el-col>
        </el-row>
      </el-card>
      <!-- 数据列表 - head-->
      <el-card class="operate-container mt10" shadow="never">
        <div style="width:100%;">
          <i class="el-icon-tickets"></i>
          <span>使用优惠券购买的商品</span>
        </div>
      </el-card>
      <el-card class="filter-container" shadow="never" style="margin-top:10px;">
        <el-table
          :data="dataShowList"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column label="商品图片" width="140">
            <template slot-scope="scope">
              <el-popover placement="right" title trigger="hover">
                <el-image
                  slot="reference"
                  :src="scope.row.imageUrl"
                  :alt="scope.row.imageUrl"
                  style="max-height: 40px;max-width: 40px"
                ></el-image>
                <el-image :src="scope.row.imageUrl" style="max-height: 350px;max-width: 350px"></el-image>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="商品名称" prop="productName"></el-table-column>
          <el-table-column label="商品价格" prop="minPrice"></el-table-column>
          <el-table-column label="付款件数" prop="saleCount"></el-table-column>
          <el-table-column label="付款人数" prop="userCount"></el-table-column>
        </el-table>
        <div style="padding:10px 0 20px 0;">
          <el-pagination
            @size-change="datahandleSizeChange"
            @current-change="datahandleCurrentChange"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="dataShowSearch.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="dataShowListTotal">
          </el-pagination>
        </div>
      </el-card>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dataShow = false">关 闭</el-button>
      </span>
    </el-dialog>
    <!-- 推广弹出框 -->
    <el-dialog title="优惠券推广" :visible.sync="extendShow" width="40%" center>
      <el-form ref="form" :model="extendForm" v-loading="extendFormLoading">
        <div class="extend">
          <el-tabs tab-position="left" v-model="activeName">
            <el-tab-pane label="H5推广" name="h5">
              <img style="width:375px;margin:auto;" :src="extendForm.couponCodeImg" alt="">
            </el-tab-pane>
            <el-tab-pane :disabled="extendForm.couponMiniCodeImg == null" label="小程序推广" name="mini">
              <img style="width:375px;margin:auto;" :src="extendForm.couponMiniCodeImg" alt="">
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="downImg">下载推广图</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getCouponInfoPage,
  deleteCouponInfo,
  couponData,
  userCouponProductPage,
  getCouponCodeUrl
} from '@/api/marketManage/coupon'
export default {
  data(){
    return{
      searchList:{
        tabType:'-1',
        pageNum:1,
        pageSize:5
      },
      category:[],
      list:[],
      listTotal:0,
      activeName:'h5',
      newShow:false,        // 新建优惠券弹出框
      couponType:'0',         // 新增优惠券类型  0=>满减券 1=>会员专属

      dataShow:false,
      dataShowSearch:{
        pageNum:1,
        pageSize:5
      },
      dataShowList:[],      
      dataShowListTotal:0,
      dataShowForm:{},
      extendShow:false,
      extendForm:{},
      activeName:'h5',
      extendFormLoading:false
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.activityType = 2
    this.getData();
  },
  methods:{
    downImg(){
      if(this.activeName == 'h5') window.open(this.extendForm.couponCodeImg)
      if(this.activeName == 'mini') window.open(this.extendForm.couponMiniCodeImg)
    },
    extendItem(e){
      let obj = {
        shopId:this.shop.shopId,
        couponId:e.id
      }
      this.extendFormLoading = true;
      this.extendShow = true
      getCouponCodeUrl(obj).then(res=>{
        console.log('推广图',res)
        if(res.code == 200){
          this.extendForm = res.data
          this.extendFormLoading = false;
        }
      })
    },
    showData(e){
      this.dataShowSearch.couponName = e.couponName
      this.dataShowSearch.cuoponId = e.id
      couponData(e.id).then(res=>{
        console.log('数据',res)
        if(res.code == 200){
          this.dataShowForm = res.data
        }
      })
      this.getList()
      this.dataShow = true;
    },
    getList(){
      userCouponProductPage(this.dataShowSearch).then(res=>{
        console.log('数据商品',res)
        if(res.code == 200){
          this.dataShowList = res.data.list
          this.dataShowListTotal = res.data.total
        }
      })
    },
    // 跳转新增优惠全页面
    goToNew(){
      console.log('couponType',this.couponType)
      if(this.couponType == 0) this.$router.push({path:'/marketManage/coupon/newCoupon'})
      if(this.couponType == 1) this.$router.push({path:'/marketManage/coupon/newMemberCoupon'})
    },
    copyItem(e){
      console.log('couponType',this.couponType)
      if(e.couponType == 0) this.$router.push({path:'/marketManage/coupon/newCoupon',query:{couponId:e.id}})
      if(e.couponType == 1) this.$router.push({path:'/marketManage/coupon/newMemberCoupon',query:{couponId:e.id}})
    },
    lookItem(e){
      if(e.couponType == 0) this.$router.push({path:'/marketManage/coupon/couponDetail',query:{couponId:e.id}})
      if(e.couponType == 1) this.$router.push({path:'/marketManage/coupon/couponMemberDetail',query:{couponId:e.id}})
    },
    // 列表 - 获取数据
    getData(){
      this.searchList.couponStatus = this.searchList.tabType == '-1' ? '' : this.searchList.tabType
      console.log('请求的数据',this.searchList)
      getCouponInfoPage(this.searchList).then(res=>{
        console.log('优惠券列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.couponName = ''
      this.searchList.couponType = ''
      this.getData()
    },
    // 已开始 - 失效
    failItem(o){
      this.$confirm('点击失效后，该优惠券变为已结束，买家之前领取的优惠券，在可用时间内还能继续使用，但失效后，其他用户不可领取', '是否优惠券失效？', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteCouponInfo(o.id).then(res=>{
          console.log('失效操作',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: res.message
            });
            this.getData();
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    // 分页
    datahandleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.dataShowSearch.pageSize = val
      this.getList();
    },
    datahandleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.dataShowSearch.pageNum = val
      this.getList();
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
  .active {
    height: 70px;
  }
  .extend /deep/ .el-tabs__nav-scroll{
    border-bottom: none;
  }
</style>